import LineChartComponent from "../utils/EchartsTest";
import {chartData} from "../../shared/data"
import React, {Component} from "react"
import {Button, Card, CardImg, CardTitle, Col} from "reactstrap";
import {Link} from "react-router-dom";
import {CardContent} from "@material-ui/core";
import {string} from "prop-types";
import {IP} from "../../shared/website";


class AdminUserBrowser extends Component{
    constructor(props) {
        super(props);
        this.state = {
            data:[],
            pageNumber:1,
            totalPage:null,
        }
        this.getUsers = this.getUsers.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleClickForNextPage = this.handleClickForNextPage.bind(this);
        this.handleClickForLastPage = this.handleClickForLastPage.bind(this);
        this.getUsers(this.state.pageNumber).then(r => null);
    }

    handleClick(event) {
        const target = event.target;
        console.log(event)
        const id = target.id;
        let now_value = target.innerText;
        let params = new URLSearchParams();
        if (now_value === "封禁") {
            params.append('status', "1");
        }
        else {
            params.append('status', "0");
        }


        fetch('http://' + IP + '/user/' + id + "/status", {
            method: 'POST',
            body: params
        })
            .then(res => res.json())
            .then((data) => {
                console.log(data);

                this.getUsers(this.state.pageNumber).then(r => null);
                alert("修改成功！");
            });
    }

    handleClickForNextPage(event) {
        if (this.state.totalPage !== null) {
            if (this.state.pageNumber + 1 > this.state.totalPage) {
                return;
            }
        }
        this.getUsers(this.state.pageNumber + 1).then(r => null);
    }

    handleClickForLastPage(event) {
        if (this.state.pageNumber - 1 <= 0) {
            return;
        }
        this.getUsers(this.state.pageNumber - 1).then(r => null);
    }

    async getUsers(pageNumber) {
        fetch('http://' + IP + '/user/page/' + pageNumber,{
            method:'GET'
        })
            .then(res =>res.json())
            .then((data) => {
                // console.log("data:", data);
                let userList = data.data;
                if (userList === undefined) {
                    alert("获取用户列表失败，未知错误！"); return;
                }
                this.setState({data:userList});
                this.setState({pageNumber:pageNumber});
                this.setState({totalPage:data.totalPage});
            });
    }

    RenderUsers(users) {
        console.log(users);
        if (users === undefined || users === null) return <div>没有用户！</div>
        let a = users.map((user)=>{
            let min = 100;
            let max = 200;
            let rand = Math.floor(min + (Math.random() * (max-min)));
            let src = "http://lorempixel.com/"+rand+"/"+rand + "/";
            if (user.usertype === 1) user.usertype = "普通用户";
            else if (user.usertype === 2) user.usertype = "系统管理员";
            let buffon_string = "封禁";
            if (user.isBlocked === 1) {
                buffon_string = "解除封禁";
            }
            return (
                <Card>
                    <div className={"row"}>
                        <div className={"col-12 col-md-2 m-1"}>
                            <CardImg  object src={src} alt={user.userid}/>
                        </div>
                        <div className={"col-12 col-md-7 m-1"}>
                            <CardTitle>用户编号:{user.userid}</CardTitle>
                            <CardContent>
                                用户名:{user.username}<br/>
                                用户类型:{user.usertype}<br/>
                                账号状态:{user.isBlocked}<br/>
                                <Button type="submit" color="primary" id={user.userid} onClick={this.handleClick}>{buffon_string}</Button>
                            </CardContent>

                        </div>

                    </div>

                </Card>
            );



        });
        return a;

    }


    render() {

        let users = this.RenderUsers(this.state.data);
        if (this.state.data === "" )return (<div></div>);
        return (
            <div className="container">
                {users}
                <Button type="submit" color="primary" onClick={this.handleClickForLastPage}>上一页</Button>
                <Button type="submit" color="danger" onClick={this.handleClickForNextPage}>下一页</Button>
                <div>当前页{this.state.pageNumber}</div>
                <div>总页数{this.state.totalPage}</div>
            </div>
        );
    }
}

export default  AdminUserBrowser;
